<template>
  <div>

    <div>
      <van-nav-bar
        title="收藏夹"
      />
    </div>
    <div>
    <div class="allnull" v-if="Mlength == 0">
      收藏为空
    </div>
      <div class="live-tab" ref="liveTabEryin">
        <ul>
          <li v-for="(item, index) in getLivedata" :key="index" ref="node" @click="viewMovieDetail(item.id)">
            <van-row gutter="20">
              <van-col span="6" class="live-img-auto">
                <van-image
                  fit="contain"
                  :src="item.images.small"
                />
              </van-col>
              <van-col span="12">
                <h4>{{item.title}}</h4>
                <div class="live-tab-name one-text">主演:{{item.hebian_casts}}</div>
                <div class="llive-tab-category one-text">类别: {{item.genres.join('/')}}</div>
                <div class="live-tab-date one-text">时间: {{item.durations[0]}}</div>
              </van-col>
              <van-col class="live-purchase" span="6">
                <div class="live-average">{{item.rating.average}}<span class="branch">分</span></div>
                <van-button class="live-purchase-Ticket" plain type="primary" color="#ff3f7e" size="small">查看</van-button>
              </van-col>
            </van-row>
          </li>
        </ul>
      </div>

    </div>

  </div>
</template>

<script>

  export default {
    name: 'shopping',

    data() {
      return {
        // 电影数据
        getLivedata: [],
        Mlength: 0,
      };
    },
    methods:{

      // 跳转详细页
      viewMovieDetail(id){
        this.$router.push({name: 'movieDetail', params: {id}});
      },

    },
    // 获取焦点
    activated(){
      let Mdata = getjsonloca('Collection', {});
      let UserName = huoquzhi('LandUser');
      Mdata = Mdata[UserName];
      if(Mdata == undefined){
        return;
      }
      for(let key in Mdata){
        let hebian = [];
        let v = Mdata[key];
        for(let i in v.casts){
          hebian.push(v.casts[i].name);
        }
        v.hebian_casts = hebian.join('/');
      }
      this.getLivedata = Mdata;
      this.Mlength = Object.keys(Mdata).length;

   }

  }
</script>

<style scoped lang="less">
  .allnull{
    text-align: center;
    color: #666;
  }
  .live-tab{
    height: calc(~"100vh - 2.4rem - 1.146rem");
    overflow-y: auto;
    overflow-x: hidden;
    ul{
      list-style: none;
      margin: 0;
      padding: 0;
      background-color: #f2f2f2;
      >li{
        padding: .2rem;
        margin: .2rem 0;
        background-color: #fff;
        h4{
          margin: 0;
          font-size: .426rem;
          margin: 0.133rem 0;
        }
        .live-purchase{
          text-align: right;
          padding-top: 0.266rem;
        }
        .live-purchase-Ticket{
          margin-top: 1rem;
        }
        .live-tab-name{
          margin: .3rem 0;
          color: #659d0e;
        }
        .live-tab-date{
          margin-top: 0.266rem;
          color: #999;
        }
        .live-average{
          color: #d36f16;
          font-size: 0.48rem;
        }
        .branch{
          font-size: 0.373rem;
        }
        .live-img-auto{
          margin: 0 auto;
          max-width: 2.666rem;
        }
      }
    }
  }
</style>
